import request from './javaRequest'

// 平台接单/拒单
export const platformDispatch = (data) => {
  return request({
    url: '/services/serveWork/platformDispatch',
    data: data,
    method: 'put',
  })
}
// 组织接单/拒单
export const orgDispatch = (data) => {
  return request({
    url: '/services/serveWork/orgDispatch',
    data: data,
    method: 'put',
  })
}

// 获取点单列表
export const getOrderList = (data) => {
  return request({
    url: '/services/serveWork/page',
    data: data,
  })
}
